@import '../../sass/utils/variables';
@import '../../sass/utils/functions';
@import '../../sass/utils/mixins';

.wrraper {
    padding: 56px 0 40px;
    color: getColor('secondary');
    text-align: center;

    @media screen and (min-width: $tablet) {
        padding: 48px 0 40px;
    }

    @media screen and (min-width: $desktop) {
        padding: 80px 0;
    }
}

.title {
    @include font(32px, 44px);

    @media screen and (min-width: $tablet) {
        @include font(40px, 54px);
    }

    @media screen and (min-width: $desktop) {
        @include font(40px, 48px);
    }
}

.subTitle {
    margin-bottom: 16px;
    display: block;
    @include font();

    @media screen and (min-width: $tablet) {
        @include font(18px, 25px);
    }

    @media screen and (min-width: $desktop) {
        @include font(18px, 33px);
    }
}

.text {
    margin-top: 24px;

    @media screen and (min-width: $tablet) {
        @include font(18px, 25px);
    }
}

.gallery {
    margin-top: 31px;
    display: grid;
    row-gap: 8px;

    @media screen and (min-width: $tablet) {
        margin-top: 30px;
        grid-template-columns: repeat(3, auto);
        row-gap: 18px;
        justify-content: space-between;
    }

    @media screen and (min-width: $desktop) {
        margin-top: 36px;
        row-gap: 24px;
    }
}

.item {
    display: block;
    width: 280px;
    @include transition-effect(opacity);
    cursor: pointer;

    &:hover, 
    &:focus {
        opacity: 0.5;
    }

    @media screen and (min-width: $tablet) {
        width: 223px;
    }

    @media screen and (min-width: $desktop) {
        width: 421px;
    }
}